import { Slot } from '@radix-ui/react-slot';
import clsx from 'clsx';
import { ButtonHTMLAttributes, FC, ReactNode } from 'react';

export interface ButtonProps extends ButtonHTMLAttributes<HTMLButtonElement> {
    children: ReactNode;
    asChild?: boolean;
    className?: string;
}

export const Button: FC<ButtonProps> = ({ children, asChild = false, className, ...props }) => {
    const Component = asChild ? Slot : 'button';

    return (
        <Component
            {...props}
            className={clsx(
                'py-3 px-4 bg-cyan-500 rounded font-semibold text-black text-sm w-full transition-colors hover:bg-cyan-300 focus:ring-2 ring-white',
                className,
            )}
        >
            {children}
        </Component>
    );
};
